<template>
 <div class="login-background">
   <form class="login">
     <h1>Registrieren</h1>
     <label>Benutzername</label>
     <input required v-model="username" type="text"/>
     <label>Passwort</label>
     <input required v-model="password" type="password"/>
     <label>Passwort Wiederholen</label>
     <input required v-model="passwordWiederholung" type="password"/>
     <hr/>
     <button type="submit" :disabled ="!passwordIsSame" @click="registrieren">Registrieren</button>
   </form>
 </div>
</template>

<script>
import Hash from 'object-hash'
import JSEncrypt from 'jsencrypt'
import axios from 'axios'

export default {
  name: 'Registration',
  props: {
  },
  data() {
    return {
      username: 'Benutzername',
      password:'',
      passwordWiederholung:''
    }
  },
  methods: {
    encrypt(msg, publicKey) {
      var jsEncrypt = new JSEncrypt();
      jsEncrypt.setPublicKey(publicKey);
      var encrypted = jsEncrypt.encrypt(msg);

      var base64result = btoa(encrypted);

      return base64result;
    },
    registrieren() {
      var publickey = ''
      var encryptedValue = ''
      var pass = this.password
      axios.post(`/User/Registration/${this.username}/PublicKey`)
        .then(response => {
          publickey = response.data.replac('-----BEGIN PUBLIC KEY-----', '').replac('-----END PUBLIC KEY-----', '').trim()
          encryptedValue = this.encrypt(pass, publickey);
          axios.post(`/User/Registration/${this.username}`, {
            passHash: encryptedValue
          })
            .then(response => {
                publickey = response.data
            })
        })
    }
  },
  computed: {
    passHash() {
      return Hash(this.password)
    },
    passwordIsSame() {
      return this.password
        ? this.password == this.passwordWiederholung
        : false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login-background{
  background-color: rgb(138 152 178 / 71%);
  margin: auto;
  width: 25%;
  min-width: 350px;
  border-radius: 5px;
}
.login{
  display: grid;
  text-align: center;
  margin: auto;
}
input{
  width: 200px;
  text-align: center;
  margin: auto;
}
h1 {
  margin-top: 40px;
  text-align: center;
  margin: auto;
}
ul {
  list-style-type: none;
  padding: 0;
}
label {
  text-align: center;
  margin: auto;
}
button {
  width: 100px;
  text-align: center;
  margin: auto;
  margin-bottom: 10px;
}
</style>
