import Vue from "vue";
import Router from "vue-router";

import Login from '../components/Identity/Login.vue'
import Registration from '../components/Identity/Registration.vue'
import HelloWorld from '../components/HelloWorld.vue'
import TestComponent from '../components/Test/TestComponent.vue'
import Bild from '../components/Test/Bild.vue'
const Bar = { template: '<div>bar</div>' }

Vue.use(Router);

const router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    { path: '/login', component: Login },
    { path: '/registration', component: Registration },
    { path: '/test/helloworld', component: HelloWorld },
    { path: '/test', component: TestComponent },
    { path: '/test/bild', component: Bild },
    { path: '/bar', component: Bar },
    {
      path: "/profile",
      name: "profile",
      component: Bild
    }
  ]
});

export default router;
